@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');

@use '_normalize.scss';

$흰색:#fff;
$진회색:#777;
$검정색:#000;
$연회색:#9b9b9b;
$너비:980px;

$작은폰트:12px;
$중간폰트:14px;
$큰폰트:16px;

.height {
    height: 51px;
}

@mixin 라인하이트() {
    line-height: 51px;
}

::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #fff;
    font-size: $큰폰트;
    font-weight: bold;
}

:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #fff;
    opacity: 1;
    font-size: $큰폰트;
    font-weight: bold;
}

::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #fff;
    opacity: 1;
    font-size: $큰폰트;
    font-weight: bold;
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #fff;
    font-size: $큰폰트;
    font-weight: bold;
}

::placeholder {
    /* Most modern browsers */
    color: #fff;
    font-size: $큰폰트;
    font-weight: bold;
}

body,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
input,
button {
    margin: 0;
    padding: 0;
}

ol,
ul {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: 'Nanum Gothic', sans-serif;
    color: $흰색;
}

.main_header {
    width: $너비;
    margin: auto;
    display: flex;
    @extend .height;

    .h1_logo {
        flex: 1;
        padding-top: 3px;
        margin-right: 10px;

        a {
            display: block;
            width: 103px;
            height: 25px;

            img {
                width: 100%;
                vertical-align: middle;
            }
        }
    }

    .navlist1 {
        flex: 7;
        display: flex;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            letter-spacing: -1.5px;

            &:not(:last-child) {
                padding-right: 12px;
                background: url('./header_line_bg.gif') no-repeat 94% 20px;
            }

            a {
                color: $진회색;
            }
        }
    }

    .navlist2 {
        flex: 2;
        display: flex;
        justify-content: end;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            padding-left: 14px;
            background: url('./header_middleLine_bg.gif') no-repeat left 24% center;

            a {
                display: block;
                width: 100%;
                color: $진회색;
                padding-left: 6px;
                letter-spacing: -2px;
            }
        }
    }
}

.mainbanner_bg {
    background: url('./main_bg.png') no-repeat;
    background-position: center top;
    margin: auto;
    min-height: 100vh;

    .mainbanner_nav {
        width: $너비;
        margin: auto;
        display: flex;
        justify-content: flex-end;
        padding-top: 20px;
        padding-right: 30px;
        font-size: $작은폰트;
        font-weight: bold;

        li {
            padding-left: 20px;

            a {
                color: $흰색;
            }
        }
    }

    .banner_contents {
        display: flex;
        width: $너비;
        margin: auto;

        .mainbanner_h2 {
            padding-left: 66px;
            padding-top: 7px;
            margin-right: 40px;
        }

        .banner_form {
            width: 464px;
            height: 50px;
            border: 1px solid #dbf8fd;
            border-radius: 4px;
            margin-top: 50px;

            input {
                background: transparent;
                width: 88%;
                height: 50px;
                border: 0 none;
                vertical-align: top;
                padding-left: 10px;
                box-sizing: border-box;
                color: #fff;
                font-size: $큰폰트;
                font-weight: bold;
            }

            button {
                background: transparent;
                width: 12%;
                height: 50px;
                border: 0 none;
                cursor: pointer;
            }
        }

        .mainbanner_list {
            width: 90px;
            height: 50px;
            margin-left: 10px;
            margin-top: 50px;
            cursor: pointer;
            background: transparent;
            border: 0 none;
        }
    }

}